@keyframes jump {
  0% {
    top: 0;
  }
  100% {
    top: -20px;
  }
}
#pulldown-arrow {
  position: relative;
  animation: jump 1s cubic-bezier(0.1, 0.76, 0.7, 0.99) infinite alternate;
  transition: all 2s;
}
@media screen and (max-width: 768px) {
  #pulldown-arrow {
    position: absolute;
    bottom: 20px;
    margin: auto;
    left: 0;
    right: 0;
    top: initial;
  }
  #pulldown-arrow:before {
    position: relative;
    animation: jump 1s cubic-bezier(0.1, 0.76, 0.7, 0.99) infinite alternate;
    transition: all 2s;
  }
}
